// /app/noticiasvetchile/page.tsx
"use client";

import React, { useState, useEffect } from "react";
import { PublicacionRenderer } from "./components";
import { Publicacion } from "@/app/types/noticias";

export default function NoticiasVetChilePage() {
  const [publicaciones, setPublicaciones] = useState<Publicacion[]>([]);
  const [publicacionActiva, setPublicacionActiva] = useState<Publicacion | null>(null);
  const [cargando, setCargando] = useState(true);

  useEffect(() => {
    // Cargar publicaciones del localStorage (en producción sería una API)
    const cargarPublicaciones = () => {
      try {
        const guardadas = localStorage.getItem("publicaciones");
        if (guardadas) {
          const parsed: Publicacion[] = JSON.parse(guardadas);
          // Solo mostrar publicadas
          const publicadas = parsed.filter(p => p.estado === "publicado");
          setPublicaciones(publicadas);
          
          // Buscar por slug en URL o usar la primera destacada o la primera
          const params = new URLSearchParams(window.location.search);
          const slug = params.get("slug");
          
          if (slug) {
            const encontrada = publicadas.find(p => p.slug === slug);
            if (encontrada) {
              setPublicacionActiva(encontrada);
            }
          } else {
            const destacada = publicadas.find(p => p.destacada);
            setPublicacionActiva(destacada || publicadas[0] || null);
          }
        }
      } catch (error) {
        console.error("Error cargando publicaciones:", error);
      } finally {
        setCargando(false);
      }
    };

    cargarPublicaciones();
  }, []);

  if (cargando) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <div className="animate-spin text-4xl mb-4">⏳</div>
          <p className="text-gray-600">Cargando...</p>
        </div>
      </div>
    );
  }

  if (!publicacionActiva) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50 p-4">
        <div className="text-center max-w-md">
          <div className="text-6xl mb-4">📭</div>
          <h1 className="text-2xl font-bold text-gray-800 mb-2">No hay publicaciones disponibles</h1>
          <p className="text-gray-600">Vuelve más tarde o contacta al administrador.</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white">
      {/* Navegación simple */}
      {publicaciones.length > 1 && (
        <nav className="bg-gray-900 text-white py-4">
          <div className="max-w-6xl mx-auto px-4 flex items-center justify-between">
            <span className="font-bold text-lg">VetChile Noticias</span>
            <select
              value={publicacionActiva.id}
              onChange={(e) => {
                const pub = publicaciones.find(p => p.id === e.target.value);
                if (pub) setPublicacionActiva(pub);
              }}
              className="bg-gray-800 text-white px-4 py-2 rounded-lg text-sm"
            >
              {publicaciones.map(pub => (
                <option key={pub.id} value={pub.id}>
                  {pub.titulo}
                </option>
              ))}
            </select>
          </div>
        </nav>
      )}
      
      {/* Contenido */}
      <main className="py-12 px-4">
        <PublicacionRenderer publicacion={publicacionActiva} />
      </main>
      
      {/* Footer */}
      <footer className="bg-gray-50 py-8 text-center text-gray-500 text-sm">
        <p>© {new Date().getFullYear()} VetChile - Todos los derechos reservados</p>
      </footer>
    </div>
  );
}
